import Vue from 'vue'
import VueRouter from 'vue-router'
import login from '../components/login.vue'
import main from '../components/main.vue'
import newboke from '../components/newboke.vue'
import hot from '../components/hot.vue'
import mymess from '../components/mymess.vue'
import myboke from '../components/myboke.vue'
import addboke from '../components/addboke.vue'
import viewboke from '../components/viewboke.vue'
import boke from '../components/boke.vue'
import othermess from '../components/othermess.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path:'/',
    redirect:'/login'
  },{
    path:'/login',
    name:'login',
    component:login
  },{
    path:'/main',
    name:'main',
    component:main,
    children:[{
      path:'/boke',
      name:'boke',
      component:boke,
      children:[{
        path:'/newboke',
        name:'newboke',
        component:newboke,
      },{
        path:'/hot',
        name:'hot',
        component:hot,
      }]
    },{
      path:'/mymess',
      name:'mymess',
      component:mymess,
      children:[{
          path:'/myboke',
          name:'myboke',
          component:myboke,
      }]
    },{
        path:'/addboke',
        name:'addboke',
        component:addboke,
    },{
        path:'/viewboke',
        name:'viewboke',
        component:viewboke,
      },{
        path:'/othermess',
        name:'othermess',
        component:othermess,
      }]
  },
]


const router = new VueRouter({
  routes,
  // mode:'history',  //去除#号
})
router.beforeEach((to,from,next)=>{
  if(to.path === '/login') return next()
  const store = window.sessionStorage.getItem("name")
  if(!store) next('/login')
  next();
})

export default router
